<template>
  <div id="CompanyIntroduction">
    <div class="banner container-fuild text-center">关于我们</div>
    <div class="section-title text-center wow fadeInDown">
      <span class="section-title__tagline">Company Dynamics</span>
      <h3 class="section-title__title ">
        公司介绍
      </h3>
    </div>
    <div class="container">
      <div class="row CompanyIntroduction-container">
        <div class="col-xs-12 col-sm-12 col-md-6 wow zoomIn">
          <img
            class="img-responsive center-block"
            src="@/assets/img/about_img.png"
          />
        </div>
        <div class="col-xs-12 col-sm-12 col-md-6 wow fadeInRight">
          <h3>高参文化发展有限公司</h3>
          <p class=".text-justify">
            高参文化发展有限公司是江西高参集团旗下子公司之一，成立于2022年。高参文化发展有限公司是一家以宣传文化、大健康文化传播及教育培训为主的文化公司。
          </p>
          <p class=".text-justify">
            通过品牌宣传、一站式健康管理计划、特色文旅、实践教育等方式，从认知和实践层面为企业、家庭及个人制定个性化健康管理计划以及教育相关解决方案。通过品牌宣传、一站式健康管理计划、特色文旅、实践教育等方式，从认知和实践层面为企业、家庭及个人制定个性化健康管理计划以及教育相关解决方案。
            高参文化发展有限公司秉承着文化传播、普及大健康文化和教育培训的使命，致力于帮助企业、家庭和个人提升文化素养、扩展视野和增强能力。我们注重个性化的服务，与客户紧密合作，根据他们的需求和目标提供量身定制的解决方案。
          </p>
          <p class=".text-justify">
            无论是通过认知还是实践，我们的目标是激发人们对文化的热爱，促进文化的传承和跨文化的交流与理解，在健康方面公司集大健康文化、技术、产品、服务、运营、数据、服务、人才专家，共同打造“健康中国”样板工程，为企业及个人提供大健康领域的综合解决方案。
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { WOW } from "wowjs";
export default {
  name: "CompanyIntroduction",
  data() {
    return {};
  },
  mounted() {
    var wow = new WOW();
    wow.init();
  }
};
</script>
<style scoped>
.banner {
  color: #fff;
  font-size: 30px;
  height: 150px;
  line-height: 150px;
  background-image: url("../assets/img/banner1.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll;
  background-position: center center;
}
.row {
  margin-right: 0;
  margin-left: 0;
}
.CompanyIntroduction-container {
  padding: 100px 0;
  color: #808080;
  transition: all ease 0.5s;
}
.CompanyIntroduction-container > div > p {
  font-size: 14px;
  line-height: 2.5rem;
}
@media screen and (max-width: 997px) {
  .CompanyIntroduction-container {
    padding: 10px 0;
    color: #808080;
  }
}
.section-title {
  margin-top: 50px;
  margin-bottom: 50px;
}

.section-title__tagline {
  display: inline-flex;
  color: #0091c7;
  font-size: 24px;
  align-items: center;
  line-height: 30px;
  margin-bottom: 2px;
  font-weight: 400;
}

.section-title__tagline::before {
  content: "";
  background-color: var(--thm-extra);
  width: 10px;
  height: 2px;
  margin-right: -s10px;
}

.section-title__title {
  margin: 0;
  font-weight: 800;
  color: var(--thm-black);
  font-size: 33px;
  line-height: 50px;
}
</style>



